.popup-delete {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: flex;
    justify-content: center;
    align-items: center;
    visibility: hidden;
    opacity: 0;
    transition: visibility .5s, opacity .5s linear;
}

@media screen and (max-width: 767px) {
    .popup-delete__text {
        text-align: center;
    }
}